import Checked from '@/static/img/icon-checked.png'
import Uncheck from '@/static/img/icon-uncheck.png'
export default {
	name: 'good',
	props: {
		item: {
			type: Object,
			default: null
		},
		level: {
			type: [String, Number],
			default: 0
		},
		edit: {
			type: Boolean,
			default: false
		},
		cart: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			good: {
				"categoryId": "",
				"categoryName": "",
				"createTime": "",
				"downTime": "",
				"hits": 0,
				"id": "",
				"imgUri": "",
				"maxprice": 0,
				"minprice": 0,
				"name": "",
				"place": "2",
				"sellTime": "",
				"spuSalesNum": 0,
				"totalStock": 0
			},
			Checked,
			Uncheck
		}
	},
	mounted() {
		this.good = { ...this.item
		}
		// this.$set(this.good, 'isCheck', false);
	},
	// filters: {
	//   fixed(val) {
	//     val = +val
	//     return val.toFixed(2);
	//   }
	// },
	methods: {
		goGoodsDetail() {
			if (this.level == 2) {
				uni.navigateTo({
					url: '/pages/order/goodsDetail/goodsDetail?shopId=' + this.good.shopId + '&goodsId=' + this.good.id
				})
			}
		},
		triggerCheck() {
			if (this.edit) {
				this.good.isEditCheck = !this.good.isEditCheck
				this.$emit('checked', this.good.isEditCheck)
			} else {
				this.good.isCheck = !this.good.isCheck
				this.$emit('checked', this.good.isCheck)
			}
		}
	}
}
